.nodePanel {
    min-height: 610px;
    min-width: 917px;
    background: #FFF;
    border-radius: 5px;
    .ptop {
        background-color: #09c;
        padding-left: 20px;
        font-size: 14px;
        border-radius: 5px 5px 0 0;
        color: #FFF;
    }

    // 说明位置样式
    .guide {
        background-color:#EEE;
        color: #666;
    }

    // 操作按钮块样式
    .controls{
        height: 40px;
        line-height: 40px;
        position: relative;

        .btns {
            position: absolute;
            height: 30px;
            line-height: 30px;
            left: 0px;
            margin-top: 5px;

            .icon {
                color: #000;
                font-size: 12px;
                margin-right: 6px;
                margin-top: -6px;
            }
        }

        .operationPosi {
            left: 200px;
        }

        .selectBtn, .operationBtn{
            border: 0;
            width: 132px;
            text-align: left;
            font-size: 12px;
            position: relative;
            cursor: pointer;
            &:hover {
                .icon {
                    color: $c_hilite;
                }
            }
        }

        .operationBtn, .operationFuncs{
            .btnName, .icon {
                height: 26px;
                line-height: 26px;
                position: absolute;
                text-align: center;
            }
            .btnName {
                border: 1px #CCC solid;
                width: 100px;
                border-radius: 5px 0 0 5px;
            }
            .icon {
                border: 1px #CCC solid;
                border-left: 0px;
                left: 102px;
                margin: 0;
                width: 29px;
                border-radius: 0 5px 5px 0;
            }
        }

        .operationFuncs {
            margin-top: 28px;
            border: 1px #CCC solid;
            border-top: 0;
            background: #FFF;
            z-index: 100;
            position: relative;
            @include boxshadow_spinner();

            .funcBtn {
                cursor: pointer;
                &:hover {
                    background-color: $c_hilite;
                    color: #FFF;
                    .icon {
                        color: #FFF;
                    }
                }
            }

            .btnName , .icon{
                border: 0;
                position: relative;
                border-radius: 0;
                left: 0px;
                display: inline-block;
                vertical-align: top;
            }

            .btnName {
                width: 80px;
                text-align: left;
            }
            .icon {
                width: 50px;
            }
        }

    }

    // 表格样式
    .nodeList {
        .nodes {
            width: 90%;
            margin: 0 auto;
        }

        .nodeListUl {
            padding: 0;
        }
        .listHeader, .nodeRow{
            height: 42px;
            line-height: 42px;
            position: relative;
            .col {
                border-left: 1px #CCC solid;
                border-bottom: 1px #CCC solid;
                border-top: 1px #CCC solid;
                vertical-align: top;
                text-align: center;
                font-size: 14px;
                overflow: hidden;
            }
            .col-end {
                border-right: 1px #CCC solid;
                width: 100 - $col_w * 11 - 1;
            }

            .icon {
                font-size: 14px;
                margin-top: -7px;
                color: #157503;
            }
        }
        .nodeRow {
            height: 41px;
            line-height:41px;
            cursor: pointer;
            &.serverNode {
                cursor: not-allowed;
                &:hover {
                    .col {
                        background-color: #FFF;
                    }
                }

                .first {
                    text-align: left;
                    .icon {
                        margin-left: 10px;
                        margin-right: 2px;
                    }
                }

                .nodeName {
                    left: 27px;
                }
            }
            .col {
                border-top: 0;
            }
            .title {
                font-size: 12px;
                position: relative;
                overflow: hidden;
            }

            .nodeName {
                position: absolute;
                left: 37px;
            }

            &.selected, &:hover{
                .col {
                    background-color: $c_hilite;
                }
            }
            &.childNode {
                .first {
                    text-align: left;
                    .icon {
                        color: #A4B135;
                        margin-left: 20px;
                        margin-right: 2px;
                    }
                }
            }

            &.reqReg {
                color: red;
                font-style: italic;
            }

            &.self {
                .col {
                    color: #FFF;
                    background-color: #40CE12;
                    .icon {
                        color: #F7E724;
                    }
                    &:hover {
                        background-color: #40CE12;
                    }
                }
            }
        }
    }

}